.App{
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(-45deg,#6D1221, #09316E);
  overflow-y: hidden;
}
.footer{
  display: flex;
  width: 100%;
  height: 80px;
  background-image: linear-gradient(100deg, rgba(8,20,107, .38), rgba(39,3,12,.23));
  z-index: 1;
  position: fixed;
  bottom: 0;
  padding: 10px 85px;
  align-items: center;
}
.footer>li{
  flex: 1;
  height: auto;
  list-style: none;
  padding: 0;
  margin: 0;
}
.footer>li>.credit {
  display: flex;
  align-items: center;
  color: #ffffff;
}
.footer>li>.credit>span{
  color: rgba(172,172,172,48);
  font-size: 18px;
  margin-right: 20px;
}
.footer>li>.credit a{
  text-decoration: none;
  display: block;
  margin-left: 10px;
}
.footer>li>.credit>img{
  float: left;
  height: 50px;
  width: 50px;
  border-radius: 50%;
}
.footer>li>.credit>a>h2.name{
  flex: 1;
  margin: 0;
  display: flex;
  align-items: start;
  width: 100%;
  color: rgba(172,172,172,48);
  font-size: 22px;
}
.footer>li>.credit>a>.title{
  color: rgba(172,172,172,48);
  font-size: 11px;
  display: block;
}
.footer>li>.utilities{
  text-align: center;
  display:block;
  float: left;
  padding: 0 40px;
}
.footer>li>.utilities .icon{
  color: rgba(172,172,172,48);
  font-size: 32px;
  margin: 0 !important;
}
.footer>li>.utilities span{
  display:block;
  color: rgba(172,172,172,48);
  margin-top: 8px;

}
.footer>li>.share{
  float: right;
  color: #EFEBEB;
  margin-right: 130px;
}
.footer>li>.share>img{
  width: 40px;
  height: 40px;
}
